#js-scroll {
  height: 200%;
}

.about__container {
  width: 50%;
  margin: 180px 0 100px 100px;
  padding: 40px;
}
.about__container p {
  margin: 60px 0;
  color: #fff;
  font-size: 46px;
  font-weight: 400;
  z-index: 2;
}
.about__container p span {
  font-family: "Oregon LDO";
}
.about__container-contact-link a {
  color: #fff;
}
.about__container-link a {
  color: #fff;
  font-size: 26px;
}

p.about__container-link {
  font-size: 26px;
}

.cursor-follow {
  position: absolute;
  background: url("../Assets/cursor.jpeg") no-repeat 50% 50%;
  background-size: cover;
  width: 300px;
  height: 400px;
  z-index: -1;
  user-select: none;
  pointer-events: none;
  transform: translate(50%, 50%);
}

@media (max-width: 900px) {
  .about__container {
    width: 70%;
    margin: 140px 0 60px 40px;
  }
  .cursor-follow {
    display: none;
  }
}